import { KVNamespace } from '@cloudflare/workers-types';

interface Env {
	userskv: KVNamespace;
}

export async function handleGetUsers(request: Request, env: Env, context: any, data: Record<string, any>): Promise<Response> {
	try {
		const value = await env.userskv.get('test');

		return new Response(JSON.stringify({ users: value }), {
			status: 200,
			headers: { 'Content-Type': 'application/json' },
		});
	} catch (error) {
		console.error('Error:', error);
		return new Response(JSON.stringify({ error: 'Internal Server Error' }), {
			status: 500,
			headers: { 'Content-Type': 'application/json' },
		});
	}
}
// Define an interface for the expected structure of the request body
interface UserRequestBody {
    name: string;
    role: string;
    email: string;
}

export async function handlePostUsers(request: Request, env: Env, context: any, data: Record<string, any>): Promise<Response> {
    try {
        // Retrieve current value from KV store
        const currentValue = await env.userskv.get('users');

        // Parse request body JSON with the specified interface
        const requestBody = await request.json() as UserRequestBody;

        // Parse the existing data as JSON or initialize as an empty array
        const existingData = currentValue ? JSON.parse(currentValue).users : [];

        // Construct the new user object
        const newUser = {
            "name": requestBody.name,
            "role": requestBody.role,
            "email": requestBody.email
        };

        // Add the new user to the array
        existingData.push(newUser);

        // Update the KV store with the modified array
        await env.userskv.put('users', JSON.stringify({ users: existingData }));

        return new Response(JSON.stringify({ users: existingData }), {
            status: 200,
            headers: { 'Content-Type': 'application/json' },
        });
    } catch (error) {
        console.error('Error:', error);
        return new Response(JSON.stringify({ error: 'Internal Server Error' }), {
            status: 500,
            headers: { 'Content-Type': 'application/json' },
        });
    }
}



