import { OpenAPIRouter } from '@cloudflare/itty-router-openapi'
import { handleGetUsers, handlePostUsers } from './users';
import { handlePostSurvey } from './createSurvey';
import { sendMailFun } from './sendMail';
const router = OpenAPIRouter()

interface Env {
	userskv: KVNamespace;
}


router.get('/getUsers', handleGetUsers);
router.post('/postUsers', handlePostUsers);
router.post('/createSurvey', handlePostSurvey);
router.post('/sendMail', sendMailFun);

export default {
  fetch: router.handle,
};